.slider {
    @media (max-width: $laptop) {
      margin: 4rem 0 0 3rem;
    }

    @media (max-width: $mobile) {
      margin: 0;
    }


    &.swiper-container {
      overflow: visible;
    }

    .swiper-slide {
        width: 32rem !important;
        height:  45.8rem !important;
        perspective: 1000px;

      img {
        object-fit: cover;
      }

      @media (max-width: $mobile) {
        width: 100% !important;
      }
    }

    &__slide-inner {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: center;
      transition: transform 0.8s;
      transform-style: preserve-3d;
    }


    /* Do an horizontal flip when you move the mouse over the flip box container */
    &__slide:hover &__slide-inner {
      transform: rotateY(180deg);
    }

    /* Position the front and back side */
    &__slide-front, &__slide-back {
      position: absolute;
      width: 100%;
      height: 100%;
      -webkit-backface-visibility: hidden; /* Safari */
      backface-visibility: hidden;
      box-shadow: 0px 3px 6px rgba(19, 53, 121, 0.58);
      overflow: hidden;
      border-radius: 25px;
    }

    /* Style the front side (fallback if image is missing) */
    &__slide-front {
      color: $white;
    }

    &__slide-front-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 101%;
      height: 101%;
      object-fit: cover;
    }

    &__slide-front-content {
      background: linear-gradient(0deg, rgba(19, 44, 107, 0.4), rgba(19, 44, 107, 0.4));
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 0 4rem;
      height: 100%;
      position: relative;
      z-index: 2;

      p {
        font-size: 2.5rem;
        line-height: 3rem;
        margin: 2rem 0 5.3rem;
        text-transform: uppercase;
        font-weight: 700;
      }
    }

    /* Style the back side */
    &__slide-back {
      padding: 6rem 3.3rem 1rem 3.3rem;
      background-color: $white;
      color: $blue;
      transform: rotateY(180deg);
      text-align: left;

      h6 {
        font-size: 2.5rem;
        line-height: 3rem;
        margin: 0 0 2rem;
        text-transform: uppercase;
        font-weight: 700;
      }

      p {
        font-size: 2rem;
        line-height: 2.4rem;
      }
    }


    .swiper-wrapper {
      padding-left: calc((100% - 140rem) / 2);
    }


  }
